/* ===============
      SETTINGS
  ================

// z-tooltips background color :
$zTooltipsBg : #666
// z-tooltips text color :
$zTooltipText: #fff
// z-tooltips arrow width :
$zTooltipArrow: 8px



/*=============================
      The rest of the code
  =============================

[data-ztooltips]
  position: relative
  overflow: visible
  box-sizing: border-box

  &:before, &:after
    opacity: 0
    pointer-events: none
    transition:
      property: opacity
      duration: .3s
      delay: .3s

  &:before
    content: attr(data-ztooltips)
    display: inline-block
    padding: .5em 1em
    background: $zTooltipsBg
    color: $zTooltipText
    position: absolute
    opacity: 0
    border-radius: 4px
    min-width: 200px
    max-width: 500px
    text-align: center
    line-height: 1.3
    box-shadow: 0 0 15px rgba(black, .3)
    font-variant: normal
    font-size: 1rem
    font-weight: normal
    z-index: 2

  &:after
    content: ""
    display: block
    border: $zTooltipArrow solid transparent
    position: absolute
    width: 0
    height: 0
    opacity: 0
    z-index: 3

  &:hover:before, &:hover:after
    opacity: 1

  &:not([class*="z-tooltips-"]), &.z-tooltips-top

    &:before
      bottom: 100%
      left: 50%
      transform: translateX(-50%)
      margin-bottom: 2*$zTooltipArrow

    &:after
      bottom: 100%
      left: 50%
      transform: translateX(-50%)
      border-top-color: $zTooltipsBg

  &.z-tooltips-bottom

    &:before
      top: 100%
      left: 50%
      transform: translateX(-50%)
      margin-top: 2*$zTooltipArrow

    &:after
      top: 100%
      left: 50%
      transform: translateX(-50%)
      border-bottom-color: $zTooltipsBg

  &.z-tooltips-left

    &:before
      right: 100%
      top: 50%
      transform: translateY(-50%)
      margin-right: 2*$zTooltipArrow

    &:after
      right: 100%
      top: 50%
      transform: translateY(-50%)
      border-left-color: $zTooltipsBg

  &.z-tooltips-right

    &:before
      left: 100%
      top: 50%
      transform: translateY(-50%)
      margin-left: 2*$zTooltipArrow

    &:after
      left: 100%
      top: 50%
      transform: translateY(-50%)
      border-right-color: $zTooltipsBg
